import Container from "@/components/Container";
import { IProductItemPrps } from "@/components/ProductItem";
import { log } from "console";
import React from "react";

interface IProductProps{

}



export default async function Porduct(props: IProductProps) {

    log
     
    const result = await fetch ("http://localhost:3004/product/2")
    const data = await result.json() as IProductItemPrps

      
    return (
        <Container>
            <div className="grid grid-cols-12">


                <div className="col-span-9 mt-8 shadow-md text-2 p-12 ">
                    <h2 className="font-serif text-3xl">{data.title}</h2>

                    <p className="text-gray-500 font-medium">{data.description}</p>

                    <p className="font-normal ">{data.id}</p>

                    <div className="mt-3">
                        <button className="m-3.5 px-3 py-1.5 bg-sky-300 rounded-2xl">click in</button>
                        <span className="mx-2"> + </span>
                        <button className="m-3.5 px-3 py-1.5 bg-sky-300 rounded-2xl">click out</button>
                    </div>

                </div>


                <div className="col-span-3">
                    <img src={data.image} alt="" />
                </div>


            </div>
        </Container>
    )
}